@import url('https://fonts.googleapis.com/css2?family=Comfortaa:wght@300..700&family=Jersey+10&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Black+Ops+One&family=Comfortaa:wght@300..700&family=Jersey+10&display=swap');

body {
  background: #eeeeee;
  font-family: "Comfortaa", sans-serif;
}

main {
  display: flex;
  margin: auto;
  justify-content: center;
}

.button {
  align-items: center;
  box-shadow: 3px 3px 4px 0 rgba(0, 0, 0, 0.1),
    -2px -2px 3px 0 rgba(255, 255, 255, 0.9);
  border-radius: 10px;
  display: flex;
  justify-content: center;
  border: none;
  padding: 12px;
  color: #154360;
}

.inner-shadow {
  filter: drop-shadow(-2px -2px 4px rgba(255, 255, 255, 0.7));
}

circle {
  transition: all 0.2s ease-out;
}

.action-container {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.column {
  display: flex;
  flex-direction: column;
}

.row {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.space-between {
  justify-content: space-between;
}

.gap16 {
  gap: 16px;
}

.gap32 {
  gap: 32px;
}

h5 {
  margin: 2px;
  color: grey;
  font-weight: 400;
}

h4 {
  margin: 8px;
}

h3 {
  margin: 0;
}

.timer {
  margin-top: 24px;
}

.center {
  justify-content: center;
}

input[type=number], select, textarea {
  background-color: #ebebeb;
  box-shadow: inset 3px 3px 3px #cfc9c9, 
          inset -3px -3px 3px #fdf5f5;
  padding: 12px;
  box-sizing: border-box;
  resize: vertical;
  border-radius: 10px;
  border: none;
}

input[type=number] button {
  background: red;
}

.dashboard {
  font-family: "Black Ops One", system-ui;
}
